import BetOrder from '../models/BetOrder'
const OpenBet = BetOrder.OpenBet
const HistoryBet = BetOrder.HistoryBet
import express from 'express';
const router = express.Router();

import moment from 'moment'

router.post('/utils-fetch-single-bet', (req, res) => {
	if(req.body.status === 'Pending'){
		OpenBet.find({user: req.user.username, orderNumber: req.body.orderNumber}, (err, result) => {
			if(err) res.status(404).send('fetch user single open bet error')
			res.json(result[0])
		})
	}else{
		HistoryBet.find({user: req.user.username, orderNumber: req.body.orderNumber}, (err, result) => {
			if(err) res.status(404).send('fetch user single history bet error')
			res.json(result[0])
		})
	}
})

export default router;

